const {
  gql
} = require('apollo-server-express');

const typeDefs = gql `
type setFavorite {
  name: String!
  openGraphImageUrl: String!
  stargazers: Int! 
  url: String!
}
type returnFavorite{
  id: ID!
  name: String!
  openGraphImageUrl: String!
  stargazers: Int! 
  url: String!
}
type Query {
  favorites: [returnFavorite!]!
}
type Mutation {
  # ç™»éŒ²
  createFav(
    name: String!
    openGraphImageUrl: String!
    stargazers: Int!
    url: String!
  ): setFavorite!
    
  # å‰Šé™¤
  deleteFav(
    id: ID!
  ): returnFavorite!
}
type Subscription {
    createFav: returnFavorite!
    deleteFav: returnFavorite!
}
`
module.exports = typeDefs;